import * as React from "react";
import MenuButton from "./menuButton";

class MobileNav extends React.Component {

  constructor(props) {
    super(props);
    this.menu = false;
  }

  toggleMenu() {
    this.menu = !this.menu;
  }

  render() {
    if (this.menu) {
      return (<div>
        <h1>Show</h1>
      </div>);
    }
    return (
        <div>
          <MenuButton onClick={this.toggleMenu}/>

        </div>
    );
  }
}

export default MobileNav;
